import React, { Component } from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import './Feature.css';

class Feature extends Component {

    render() {

        const image = this.getImage();
        const text = this.getText();

        const content = <Row className="vertical-center">
            {image}
            {text}
        </Row>;

        return (
            <section id={this.props.id} className="text-sm-left feature">
                <Container>
                    {content}
                </Container>
            </section>
        );
    }

    getImage() {
        return <Col sm className={this.props.layoutTextLeft ? "text-center order-sm-last" : "text-center"} data-aos="fade-up">
            {this.props.display}
        </Col>;
    }

    getText() {
        return <Col data-aos="fade-up">
            <h2 className="display-4">{this.props.title}</h2>
            <br/>
            <p className="lead">{this.props.content}</p>
            <a href={this.props.href}>Find out more</a>
        </Col>;
    }

}

export default Feature;