import React, { Component } from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';

import Division from './Division.js';

import './Home.css';

class Home extends Component {

    render() {
        return (
            <section id={this.props.id} className="gradient text-white vertical-center">

                <Division color="white" />

                <Container>

                    <Row data-aos="fade-up" data-aos-duration="1500">

                        <Col md className="text-md-left mt-5 mt-lg-0">

                            <div>
                                <h1 className="display-3">{this.props.title}</h1>
                                <p className="lead">{this.props.content}</p>
                            </div>

                            <ButtonToolbar className='my-5'>
                                <Col lg className='px-0 pr-lg-2'>
                                    <Button block variant='primary' size='lg' href={this.props.href} className='drop-shadow-dark mb-2 mb-lg-0'>Discover more</Button>
                                </Col>
                                <Col lg className='px-0 pl-lg-2'>
                                    <Button block variant='secondary' size='lg' href='#location-inference' className='drop-shadow-dark mt-2 mt-lg-0' style={{'background': 'rgba(255,255,255,0.3)' }}>Projects</Button>
                                </Col>
                            </ButtonToolbar>

                        </Col>

                        <Col md className="text-center vertical-center">
                            {this.props.display}
                        </Col>

                    </Row>

                </Container>

            </section>
        );
    }

}

export default Home;