import React, { Component } from 'react';

import Jumbotron from 'react-bootstrap/Jumbotron';

import './Paper.css';

class Paper extends Component {

    getKeywords () {
        return this.props.keywords.join(', ');
    }

    render () {
        return (
            <section id={this.props.id} className="text-left">

                <Jumbotron className="paper drop-shadow gradient text-light" data-aos="fade-up">

                    <h1 className="display-4">
                        {this.props.title}
                    </h1>
                    <p className="lead">
                        <strong>Abstract</strong> – {this.props.abstract}
                    </p>
                    <p className="lead">
                        <strong>
                            Keywords – {this.getKeywords()}
                        </strong>
                    </p>

                </Jumbotron>

            </section>
        );
    }

}

export default Paper;