import React, { Component } from 'react';

import Card from 'react-bootstrap/Card';

import './Ide.css';

class Ide extends Component {

    render() {
        return (
            <Card id="ide" bg="dark" text="white" className="drop-shadow">

                <Card.Header>
                    <span className="float-left">
                        {this.getButton("#ff443a")}
                        {this.getButton("#ffd60a")}
                        {this.getButton("#32d74b")}
                    </span>
                    graemerjbrown.github.io
                </Card.Header>

                <Card.Body>
                    <Card.Text className="text-left">
                        <code style={{ 'font-size': '1.6rem' }}>
                            <p>
                                <span style={{ 'color': '#4682b4' }}>import</span>
                                &nbsp;
                                <span style={{ 'color': '#66d9ef' }}>React</span>
                                <span style={{ 'color': '#ffffff' }}>,&nbsp;{"{"}</span>
                                &nbsp;
                                <span style={{ 'color': '#66d9ef' }}>Component</span>
                                &nbsp;
                                <span style={{ 'color': '#ffffff' }}>{"}"}</span>
                                &nbsp;
                                <span style={{ 'color': '#4682b4' }}>from</span>
                                &nbsp;
                                <span style={{ 'color': 'orange' }}>'react'</span>
                                <span style={{ 'color': '#ffffff' }}>;</span>
                            </p>
                            <p>
                                <span style={{ 'color': '#4682b4' }}>class</span>
                                &nbsp;
                                <span style={{ 'color': '#66d9ef' }}>Feature</span>
                                &nbsp;
                                <span style={{ 'color': '#4682b4' }}>extends</span>
                                &nbsp;
                                <span style={{ 'color': '#66d9ef' }}>Component</span>
                                &nbsp;
                                <span style={{ 'color': '#ffffff' }}>{"{"}</span>
                            </p>
                            <p>
                                &nbsp;&nbsp;
                                <span style={{ 'color': '#66d9ef' }}>render</span>
                                <span style={{ 'color': '#ffffff' }}>()&nbsp;{"{"}</span>
                            </p>
                            <p>
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <span style={{ 'color': '#4682b4' }}>const</span>
                                &nbsp;
                                <span style={{ 'color': '#66d9ef' }}>image</span>
                                &nbsp;
                                <span style={{ 'color': '#ffffff' }}>=</span>
                                &nbsp;
                                <span style={{ 'color': '#4682b4' }}>this</span>
                                <span style={{ 'color': '#ffffff' }}>.</span>
                                <span style={{ 'color': '#66d9ef' }}>getImage</span>
                                <span style={{ 'color': '#ffffff' }}>();</span>
                            </p>
                            <p>
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <span style={{ 'color': '#4682b4' }}>const</span>
                                &nbsp;
                                <span style={{ 'color': '#66d9ef' }}>content</span>
                                &nbsp;
                                <span style={{ 'color': '#ffffff' }}>=</span>
                                &nbsp;
                                <span style={{ 'color': '#4682b4' }}>this</span>
                                <span style={{ 'color': '#ffffff' }}>.</span>
                                <span style={{ 'color': '#66d9ef' }}>getContent</span>
                                <span style={{ 'color': '#ffffff' }}>();</span>
                            </p>
                        </code>
                    </Card.Text>
                </Card.Body>

            </Card>
        );
    }

    getButton(color) {
        return (
            <svg height="40" width="40">
                <circle cx="20" cy="20" r="10" fill={color} />
            </svg>
        );
    }

}

export default Ide;