import React, { Component } from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import Paper from './Paper';

import './Papers.css';

class Papers extends Component {

    getKeywords () {
        return this.props.keywords.join(', ');
    }

    render () {
        return (
            <section id={this.props.id} className="text-left bg-light">

                <Container>
                    <Row>

                        <Col md>

                            <Paper
                                id='location-inference'
                                title='Inferring the Home Location of Twitter Users'
                                abstract='Current content-based approaches to location inference on Twitter have relied on individual words to derive context;
                                oftentimes yielding ineffective results.
                                Our goal is to extract meaningful named entities which are highly valuable for location inference,
                                while limiting the impact of inherent noise from the vast range of topics present in tweets;
                                given such information, we aim to estimate user’s home locations with greater accuracy over existing word-based techniques.
                                This paper presents an outline of a proposed system to jointly extract named entities and evaluate their geographic distribution.
                                Additionally, an algorithm is developed for the efficient collection of user timelines leveraging the streaming API alongside a user’s friend network.
                                Results show that named entities preserve tweet semantics more precisely than individual words and can further disambiguate terms,
                                suggesting an encouragingly novel approach that promises to improve the accuracy and granularity of home location inference.'
                                keywords={
                                    [
                                        'Twitter',
                                        'location inference',
                                        'local entities',
                                        'named entity recognition'
                                    ]
                                } />

                        </Col>
                    </Row>

                        <hr />

                    <Row>
                        <Col md>
                            <Paper
                                id='systematic-review'
                                title='Cohesion and Coupling Measures Indicating Code Quality in Object Oriented Applications - A Systematic Review'
                                abstract='Software quality impacts all aspects of a system from design through to testing and maintenance.
                                High cohesion and loose coupling have been reported as desirable properties of object-oriented systems.
                                This study identifies useful measures of cohesion and coupling that have been empirically validated on object-oriented software to understand which aspects of quality they impact upon.
                                Evidence was gathered by a systematic literature review against two focused questions, a search of relevant databases identifies 8 studies published in the last decade based on inclusion criteria.
                                Results highlight that research in measures of cohesion and coupling has been active in the last decade;
                                this paper summarises such research with 30 metrics linked to attributes as indicators of code quality.
                                This review concludes that there is no single metric that can explain quality of a system;
                                each metric has its own merits in predicting a specific quality attribute.'
                                keywords={
                                    [
                                        'Code quality',
                                        'metrics',
                                        'cohesion',
                                        'coupling',
                                        'object-oriented',
                                        'systematic review'
                                    ]
                                } />
                        </Col>

                    </Row>
                </Container>

            </section>
        );
    }

}

export default Papers;